@import "src/sass/style.scss";











































.hidden {
  visibility: hidden;
}
.contact-form {
  max-width: px-to-em(500);
}
.form__control {
  position: relative;
  padding: 1em 0;
  label {
    padding-bottom: 0.5em;
  }
}
.form__text,
.form__textarea {
  width: 100%;
  padding: 0.5em;
  margin-top: 0.5em;
  background: $athensgray;
  border: 1px solid $midgray;
  border-radius: 3px;
  &:active,
  &:focus {
    color: inherit;
    background: transparent;
  }
}
.submit {
  width: 100%;
  padding: 0.5em;
  color: $white;
  text-align: center;
  background: $bondiblue;
  border: 1px solid $midgray;
  border-radius: 3px;
  &:hover {
    cursor: pointer;
    background: darken($bondiblue, 10%);
  }
}
